@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #e4c2d2;
  color: #000;
  overflow: hidden;
}

.App{
  overflow: hidden;
}


.Logo h1 {
  font-size: 2em;
  font-family: "Playfair Display";
  padding: 0 0.5em;
}

.Logo span {
  color: #ff0000;
}

@keyframes poing {
  from {
    margin-left: -10em;
  }
  to {
    margin-left: 0em;
  }
}

.hamburger svg {
  margin-top: 0.6em;
  width: 30px;
  height: auto;
}

.hamburger .rectangle{
	background: #000;
	margin-top: 7px;
	width: 30px;
	height: 4px;
	border-radius: 20px;
	transition: all 500ms ease;
}

.fill-white .rectangle{
	background: #fff;
}

.fill-white .top{
	transform: rotate(-45deg) translateY(7.5px);
}

.fill-white .middle{
	display: none;
}

.fill-white .bottom{
	transform: rotate(45deg) translateY(-7.5px);
}

.fill-white svg path {
  fill: #fff;
}

.fill-white h1 {
  color: #fff;
}

.hamburger {
  padding: 0 0.5em;
  border-radius: 50px;
}

.hamburger,
.Logo,
.Trending,
.SignUp button,
.Cart {
  cursor: pointer;
}

/* .hamburger:hover{
	background: #1f21231e;
} */

.Trending,
.Cart {
  display: flex;
  align-items: center;
}

.Trending h1,
.Cart h1 {
  font-family: "Poppins";
  font-weight: 300;
  padding: 0 1em;
}

.Trending svg {
  width: 20px;
  height: auto;
}

.SignUp button {
  outline: none;
  border: none;
  font-family: Poppins;
  font-size: 1em;
  padding: 0.5em 2.5em;
  margin: 0 1em;
  border-radius: 15px;
  background: #fff;
}

.SignUp button:hover {
  background: rgb(248, 248, 248);
}

.Cart {
  margin: 0.3em 1m;
}

.Cart_svg svg {
  padding: 0.3em 0;
  width: 35px;
  height: auto;
}


.navwidth {
  max-width: 95vw;
  margin: 0 auto;
}

.boxwidth {
  max-width: 90vw;
  margin: 0 auto;
}

.Left,
.Right {
  display: flex;
  align-items: center;
  z-index: 1;
  transition: all 500ms ease;
}

/* Navigation */

.Navigation {
  /* color: #f6f7f9; */
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 1;
}

.Nav {
  margin-top: 1em;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* Sidebar */
.sidebar,
.blur {
  position: absolute;
  top: 7px;
  width: 0;
  max-width: 95vw;
  height: 98.5vh;
  overflow: hidden;
  background: rgba(0, 0, 0, 0.3);
  -webkit-backdrop-filter: blur(22px);
          backdrop-filter: blur(22px);
  border-radius: 20px;
}

.Leftbar .sidebar,
.Leftbar .blur{
  left: 7px;
}

.Rightbar .sidebar,
.Rightbar .blur{
  right: 7px;
}

.sidebar_show,
.blur_show {
  width: 25em;
  transition: width 500ms ease;
}

.sidebar ul {
  list-style: none;
  margin: 5em 0;
}

.sidebar ul li{
  margin: 0.3em;
  border-radius: 10px;
  padding: 1em 2em;
  font-size: 1.5em;
  text-decoration: none;
  font-family: Poppins;
  color: #fff;
  cursor: pointer;
}

.sidebar p{
  margin: 0.3em;
  padding: 1em 2em;
  font-size: 1.5em;
  color: #fff;
  font-family: Poppins;
  width: 19em;
}

.sidebar ul li:hover {
  background: #1f21236c;
}


.Hero_title {
  margin-top: 13em;
}

.Hero_title h1 {
  font-size: 21em;
  color: #1f2123;
  transition: all 500ms ease;
}

.Hero_title span {
  color: #ab6262;
}

.small_title {
  margin-top: 9em;
}

.small_title h1 {
  font-size: 10em;
}

.Heropar {
  margin-top: -0.3em;
}

/* Background */

.background .heroback {
  width: 70vh;
  position: fixed;
  bottom: 0;
  right: 3em;
}

.ribbon {
  position: absolute;
  top: 34em;
  z-index: -1;
  width: 100vw;
  height: 3em;
  background: #fff;
  transition: opacity 500ms ease;
}

.vanish {
  opacity: 0;
}

.appear {
  opacity: 100;
}

.Hero_subtext h2 {
  font-family: Roboto;
  color: #1f2123;
  font-size: 4em;
}

.Hero_subtext button h3 {
  font-family: Poppins;
  font-size: 2em;
}

.Hero_subtext h3 {
  font-family: Poppins;
  font-size: 1em;
}

.Hero_subtext h3 span {
  color: #fd2323;
  cursor: pointer;
}

.Hero_subtext button {
  display: inline;
  padding: 1em 4em;
  outline: none;
  border: none;
  margin: 1em auto;
  border-radius: 50px;
  margin-top: 2em;
  background: #fff;
  cursor: pointer;
  min-width: 22em;
}

.Hero_subtext .button-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Hero_subtext svg {
  margin: 0 1em;
  width: 35px;
  height: auto;
}

.Option-box {
  width: 40%;
  text-align: center;
}

.scroller {
  position: absolute;
  bottom: 0.5em;
  width: 100vw;
}

.overlay {
  border-radius: 20px;
  margin: 0 auto;
  width: 95vw;
  height: 4em;
  display: flex;
  justify-content: center;
  cursor: pointer;
  transition: all 500ms ease;
}

.overlay:hover {
  background: rgba(0, 0, 0, 0.25);
}

.overlay:hover svg path {
  fill: rgba(228, 228, 228, 0.938);
}

.overlay svg {
  width: 20px;
  height: auto;
  transition: all 500ms ease;
}

.overlay svg path {
  fill: rgba(49, 49, 49, 0.938);
}

.rotate {
  transform: rotate(180deg);
}

/*------------------------------------------
  Responsive Grid Media Queries - 1280, 1024, 768, 480
   1280-1024   - desktop (default grid)
   1024-768    - tablet landscape
   768-480     - tablet 
   480-less    - phone landscape & smaller
--------------------------------------------*/

@media all and (max-width: 1700px){
  .Hero_subtext{
    max-width: 65vw;
  }
  .big_title h1 {
    font-size: 17em;
  }

  

  .Hero_subtext button{
    padding: 1em 4em;
  }

  .Hero_subtext .button-content h3{
    font-size: 1.6em;
    text-align: center;
  }

  .Hero_subtext svg {
    margin: 0 1em;
    width: 30px;
    height: auto;
  }

}

@media all and (max-width: 1500px){
  .Hero_subtext button{
    padding: 1em 3em;
  }

  .Hero_subtext .button-content h3{
    font-size: 1.4em;
  }
} 

@media all and (max-width: 1280px) {
  .big_title h1 {
    font-size: 13em;
  }
  
  .big_title {
    margin-top: 18em;
  }

  .small_title h1{
    font-size: 6em;
  }

  .Hero_subtext h2{
    font-size: 2em;
  }

  .background .heroback{
    right: -4em;
  }

  .Hero_subtext button{
    padding: 1em 3em;
  }

  .Hero_subtext .button-content h3{
    font-size: 1.3em;
  }

  .Option-box{
    width: 60%;
  }

 }

@media all and (max-width: 1024px) {
  .big_title h1{
    font-size: 11em;
  }

  .small_title{
    margin-top: 15em;
  }

  .Option-box{
    width: 60%;
  }
 }
 
@media all and (max-width: 835px) {

  .Option-box{
    width: 70%;
  }

  .background .heroback{
    z-index: -1;
  }
 }
 
@media all and (max-width: 480px) { 
  .Hero_title h1 {
    font-size: 6em;
    /* margin-left: -0.3em; */
  }

  .Hero_title {
    margin-top: 20em;
  }

  .Trending h1,.Cart h1, .SignUp{
    display: none;
  }

  .Cart{
    margin-right: 1.5em;
  }

  .background .heroback{
    z-index: -1;
    top: 5em;
    left: 0;
  }

  .ribbon{
    top: 10em;
    z-index: -2;
  }

  .Hero .vanish{
    opacity: 100;
  }

  .Hero_subtext h2{
    display: none;
  }

  .Hero_subtext button{
    padding: 1em 2em;
  }

  .Hero_subtext svg {
    margin: 0 1em;
    width: 30px;
    height: auto;
  }
  
  .Hero_subtext .button-content h3{
    font-size: 1.3em;
  }

  .Hero_subtext h3 span {
    color: #e20000;
  }
  
  .Option-box{
    width: 100%;
  }

  .scroller{
    display: none;
  }
}
